import { useState, useEffect, useRef } from 'react';
import { renderToString } from '@wordpress/element';

import { tabController } from '../../Components/Helper/functions';

import Settings from './Settings';
import Style from './Style';
import Accordions from './Accordions';
import { runAccordion } from './utils/functions';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { accordions, layout } = attributes;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	const [activeIndex, setActiveIndex] = useState(0);
	const babAccordion = useRef(null);

	useEffect(() => {
		babAccordion.current.innerHTML = '';
		babAccordion.current.innerHTML = renderToString(<Accordions accordions={accordions} />);

		runAccordion(attributes, clientId, true, { activeIndex });
	}, [accordions, layout]);

	useEffect(() => {
		const allDT = document.querySelectorAll(`#babAccordion-${clientId} dt`);

		allDT.forEach((dt, index) => {
			dt.addEventListener('click', () => setActiveIndex(index));
		});
	}, [attributes.accordions?.length]);

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} activeIndex={activeIndex} setActiveIndex={setActiveIndex} />

		<div className={className} id={`babAccordion-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} />

			<dl className={`babAccordion ${layout || 'horizontal'}`} ref={babAccordion}></dl>
		</div>
	</>;
};
export default Edit;