import { __ } from '@wordpress/i18n';
import { AlignmentToolbar, BlockControls, InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, PanelRow, TextControl, TextareaControl, Button, Dashicon, ButtonGroup, Tooltip } from '@wordpress/components';
import produce from 'immer';

// Settings Components
import Title from '../../Components/Title';
import Typography from '../../Components/Typography';
import ColorsControl from '../../Components/ColorsControl';
import { tabController } from '../../Components/Helper/functions';
import { gearIcon } from '../../Components/Helper/icons';

import { horizontalLineIcon, verticalLineIcon } from './utils/icons';
import { generalStyleTabs } from './utils/options';

const Settings = ({ attributes, setAttributes, activeIndex, setActiveIndex }) => {
	const { accordions, layout, textAlign, titleTypo, titleColors, titleActiveColors, contentTypo, contentColors } = attributes;

	const addAccordion = () => {
		setAttributes({
			accordions: [...accordions, {
				title: `Accordion ${accordions.length + 1}`,
				content: 'Lorem ipsum dolor sit amet consectetuer facilisis lacinia sapien ac et. Quis hendrerit neque congue pretium iaculis justo laoreet orci elit condimentum. Eros natoque Curabitur accumsan eget quis porttitor Sed Vestibulum amet sed.'
			}]
		});
		setActiveIndex(accordions.length);
	}

	const updateAccordion = (type, val) => {
		const newAccordions = [...accordions];

		newAccordions[activeIndex][type] = val;
		setAttributes({ accordions: newAccordions });
	}

	const duplicateAccordion = e => {
		e.preventDefault();
		setAttributes({ accordions: [...accordions.slice(0, activeIndex), { ...accordions[activeIndex] }, ...accordions.slice(activeIndex)] });

		setActiveIndex(activeIndex + 1);
	}

	const removeAccordion = e => {
		e.preventDefault();
		setAttributes({ accordions: [...accordions.slice(0, activeIndex), ...accordions.slice(activeIndex + 1)] });

		setActiveIndex(0 === activeIndex ? 0 : activeIndex - 1);
	}

	const { title, content } = accordions[activeIndex] || {};

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' === tab.name && <>
					<PanelBody className='bPlPanelBody help' title={__('Help', 'accordion')} initialOpen={false}>
						{/* <div className='helpItem'>
							<a href='https://bblockswp.com/docs/accordion-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'accordion')}</a>
						</div> */}

						<div className='helpItem rateUs'>
							<a href='https://wordpress.org/support/plugin/b-accordion/reviews/#new-post' target='_blank' rel='noreferrer'>
								<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'accordion')}</span>
								<span>{__('We are new and we need your help to grow!🙏', 'accordion')}</span>
							</a>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody addRemoveItems editItem' title={__('Add or Remove accordion', 'accordion')}>
						{null !== activeIndex && <>
							<h3 className='bplItemTitle'>{__(`Accordion No ${activeIndex + 1}:`, 'accordion')}</h3>

							<PanelRow className='mt20'>
								<Title className=''>{__('Title:', 'accordion')}</Title>
								<TextControl value={title} onChange={val => updateAccordion('title', val)} placeholder={__('Accordion Title', 'accordion')} />
							</PanelRow>

							<Title>{__('Content:', 'accordion')}</Title>
							<TextareaControl value={content} onChange={val => updateAccordion('content', val)} placeholder={__('Content of the accordion', 'accordion')} />

							<PanelRow className='itemAction mt20 mb15'>
								{1 < accordions.length && <Button className='removeItem' label={__('Remove', 'accordion')} onClick={removeAccordion} ><Dashicon icon='no' />{__('Remove', 'accordion')}</Button>}

								<Button className='duplicateItem' label={__('Duplicate', 'accordion')} onClick={duplicateAccordion}>{gearIcon}{__('Duplicate', 'accordion')}</Button>
							</PanelRow>
						</>}

						<div className='addItem'>
							<Button label={__('Add New Accordion', 'accordion')} onClick={addAccordion}><Dashicon icon='plus' />{__('Add New Accordion', 'accordion')}</Button>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Accordion Settings', 'accordion')} initialOpen={false}>
						<PanelRow>
							<Title className='mb5'>{__('Layout:', 'cards-block')}</Title>
							<ButtonGroup className='bPlBtnGroup'>
								<Tooltip text={__('Vertical', 'cards-block')} position='top'>
									<Button icon={verticalLineIcon} isMedium isPrimary={'vertical' === layout} aria-pressed={'vertical' === layout} onClick={() => setAttributes({
										layout: 'vertical'
									})}></Button>
								</Tooltip>

								<Tooltip text={__('Horizontal', 'cards-block')} position='top'>
									<Button icon={horizontalLineIcon} isMedium isPrimary={'horizontal' === layout} aria-pressed={'horizontal' === layout} onClick={() => setAttributes({
										layout: 'horizontal'
									})}></Button>
								</Tooltip>
							</ButtonGroup>
						</PanelRow>
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Title', 'accordion')}>
						<Typography value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: { desktop: 16, tablet: 16, mobile: 16 }, fontWeight: 700 }} produce={produce} />

						<ColorsControl value={titleColors} onChange={val => setAttributes({ titleColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />

						<ColorsControl label={__('Active Colors:', 'accordion')} value={titleActiveColors} onChange={val => setAttributes({ titleActiveColors: val })} defaults={{ color: '#fff', bg: '#8344c5' }} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Content', 'accordion')} initialOpen={false}>
						<Typography value={contentTypo} onChange={val => setAttributes({ contentTypo: val })} defaults={{ fontSize: { desktop: 15, tablet: 15, mobile: 15 } }} produce={produce} />

						<ColorsControl value={contentColors} onChange={val => setAttributes({ contentColors: val })} defaults={{ color: '#333', bg: '#ecf0f1' }} />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={textAlign} onChange={val => setAttributes({ textAlign: val })} />
		</BlockControls>
	</>;
};
export default Settings;